import { create } from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { Task } from '@/types';

// 
interface TaskStats {
  total: number;
  pending: number;
  running: number;
  completed: number;
  failed: number;
}

interface DAGNode {
  id: string;
  label: string;
  color?: string;
  shape?: string;
}

interface DAGEdge {
  from: string;
  to: string;
  color?: string;
  label?: string;
}

interface TasksState {
  // 
  tasks: Task[];
  selectedTask: Task | null;
  taskStats: TaskStats | null;
  currentPlan: string | null;
  currentWorkflowId: string | null;
  
  // DAG
  dagNodes: DAGNode[];
  dagEdges: DAGEdge[];
  dagLayout: 'hierarchical' | 'force' | 'circular';
  
  // 
  filters: {
    status: string[];
    task_type: string[];
    search_query: string;
  };
  
  // 
  setTasks: (tasks: Task[]) => void;
  addTask: (task: Task) => void;
  updateTask: (id: number, updates: Partial<Task>) => void;
  removeTask: (id: number) => void;
  setSelectedTask: (task: Task | null) => void;
  setCurrentPlan: (planTitle: string | null) => void;
  setCurrentWorkflowId: (workflowId: string | null) => void;
  setTaskStats: (stats: TaskStats | null) => void;
  
  // DAG
  setDagData: (nodes: DAGNode[], edges: DAGEdge[]) => void;
  updateNodePosition: (nodeId: string, position: { x: number; y: number }) => void;
  setDagLayout: (layout: TasksState['dagLayout']) => void;
  
  // 
  setFilters: (filters: Partial<TasksState['filters']>) => void;
  clearFilters: () => void;
  
  // 
  getFilteredTasks: () => Task[];
  getTaskStats: () => {
    total: number;
    pending: number;
    running: number;
    completed: number;
    failed: number;
  };
}

export const useTasksStore = create<TasksState>()(
  subscribeWithSelector((set, get) => ({
    // 
    tasks: [],
    selectedTask: null,
    taskStats: null,
    currentPlan: null,
    currentWorkflowId: null,
    dagNodes: [],
    dagEdges: [],
    dagLayout: 'hierarchical',
    filters: {
      status: [],
      task_type: [],
      search_query: '',
    },

    // 
    setTasks: (tasks) => {
      set(() => {
        const { nodes, edges } = generateDagData(tasks);
        const rootTask = tasks.find((task) => task.task_type === 'root');
        return {
          tasks,
          dagNodes: nodes,
          dagEdges: edges,
          currentPlan: rootTask?.name ?? null,
        };
      });
    },

    // 
    addTask: (task) => set((state) => {
      const tasks = [...state.tasks, task];
      const { nodes, edges } = generateDagData(tasks);
      return { tasks, dagNodes: nodes, dagEdges: edges };
    }),

    // 
    updateTask: (id, updates) => set((state) => {
      const tasks = state.tasks.map((task) =>
        task.id === id ? { ...task, ...updates } : task
      );
      const { nodes, edges } = generateDagData(tasks);
      return { 
        tasks, 
        dagNodes: nodes, 
        dagEdges: edges,
        selectedTask: state.selectedTask?.id === id 
          ? { ...state.selectedTask, ...updates } 
          : state.selectedTask
      };
    }),

    // 
    removeTask: (id) => set((state) => {
      const tasks = state.tasks.filter((task) => task.id !== id);
      const { nodes, edges } = generateDagData(tasks);
      return { 
        tasks, 
        dagNodes: nodes, 
        dagEdges: edges,
        selectedTask: state.selectedTask?.id === id ? null : state.selectedTask
      };
    }),

    // 
    setSelectedTask: (task) => set({ selectedTask: task }),

    // 
    setCurrentPlan: (planTitle) => set({ currentPlan: planTitle }),

    setCurrentWorkflowId: (workflowId) => set({ currentWorkflowId: workflowId }),

    setTaskStats: (stats) => set({ taskStats: stats }),

    // DAG
    setDagData: (nodes, edges) => set({ dagNodes: nodes, dagEdges: edges }),

    // 
    updateNodePosition: (nodeId, position) => set((state) => ({
      dagNodes: state.dagNodes.map((node) =>
        node.id === nodeId ? { ...node, ...position } : node
      ),
    })),

    // DAG
    setDagLayout: (layout) => set({ dagLayout: layout }),

    // 
    setFilters: (filters) => set((state) => ({
      filters: { ...state.filters, ...filters },
    })),

    // 
    clearFilters: () => set({
      filters: {
        status: [],
        task_type: [],
        search_query: '',
      },
    }),

    // 
    getFilteredTasks: () => {
      const { tasks, filters } = get();
      return tasks.filter((task) => {
        // 
        if (filters.status.length > 0 && !filters.status.includes(task.status)) {
          return false;
        }
        
        // 
        if (filters.task_type.length > 0 && !filters.task_type.includes(task.task_type)) {
          return false;
        }
        
        // 
        if (filters.search_query) {
          const query = filters.search_query.toLowerCase();
          return task.name.toLowerCase().includes(query);
        }
        
        return true;
      });
    },

    // 
    getTaskStats: () => {
      const tasks = get().tasks;
      return {
        total: tasks.length,
        pending: tasks.filter(t => t.status === 'pending').length,
        running: tasks.filter(t => t.status === 'running').length,
        completed: tasks.filter(t => t.status === 'completed').length,
        failed: tasks.filter(t => t.status === 'failed').length,
      };
    },
  }))
);

// DAG
function generateDagData(tasks: Task[]): { nodes: DAGNode[]; edges: DAGEdge[] } {
  const nodes: DAGNode[] = tasks.map((task) => ({
    id: task.id.toString(),
    label: task.name.replace(/^\[.*?\]\s*/, ''), // 
    group: task.task_type,
    status: task.status,
    level: task.depth,
  }));

  const edges: DAGEdge[] = [];
  
  // parent_id
  tasks.forEach((task) => {
    if (task.parent_id) {
      edges.push({
        from: task.parent_id.toString(),
        to: task.id.toString(),
        label: 'contains',
        color: '#1890ff',
      });
    }
  });

  return { nodes, edges };
}
