import { useEffect } from 'react';
import { message } from 'antd';
import ChatLayout from '@components/layout/ChatLayout';
import { useSystemStore } from '@store/system';
import { checkApiHealth } from '@api/client';

function App() {
  const { setSystemStatus, setApiConnected } = useSystemStore();

  // 
  useEffect(() => {
    const handleError = (event: ErrorEvent) => {
      console.error('Unhandled error:', event.error || event.message);
      message.error(`: ${event.message || ''}`, 6);
    };
    const handleRejection = (event: PromiseRejectionEvent) => {
      const reason = (event.reason && (event.reason.message || event.reason.toString())) || '';
      console.error('Unhandled rejection:', event.reason);
      message.error(`: ${reason}`, 6);
    };

    window.addEventListener('error', handleError);
    window.addEventListener('unhandledrejection', handleRejection);

    const initializeApp = async () => {
      console.log(' Initializing AI Task Orchestration System...');
      console.log(' Running in PRODUCTION mode - using REAL APIs (No Mock)');
      
      try {
        // 
        await new Promise(resolve => setTimeout(resolve, 500));
        
        const healthData = await checkApiHealth();
        
        console.log(' Health check result:', healthData);
        
        setApiConnected(healthData.api_connected);
        setSystemStatus({
          api_connected: healthData.api_connected,
          database_status: healthData.api_connected ? 'connected' : 'disconnected',
          active_tasks: 0,
          total_plans: 0,
          system_load: {
            cpu: 0,
            memory: 0,
            api_calls_per_minute: 0,
          },
        });

        if (healthData.api_connected) {
          message.success(' ', 5);
          console.log(' GLM API Status:', healthData.llm_status);
        } else {
          console.error(' API connection failed');
          message.error('  http://localhost:8000', 10);
          setApiConnected(false);
        }
      } catch (error) {
        console.error(' App initialization failed:', error);
        message.error(' ', 10);
        setApiConnected(false);
        setSystemStatus({
          api_connected: false,
          database_status: 'disconnected',
          active_tasks: 0,
          total_plans: 0,
          system_load: {
            cpu: 0,
            memory: 0,
            api_calls_per_minute: 0,
          },
        });
      }
    };

    initializeApp();

    return () => {
      window.removeEventListener('error', handleError);
      window.removeEventListener('unhandledrejection', handleRejection);
    };
  }, []); // store

  return <ChatLayout />;
}

export default App;
